.wrap{
    display: flex;
    max-width: 90vw;
    min-height:110vh;
    max-height: 120vh;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 5vh;
}

.contact{
    display: flex;
    max-width: 100vw;
    min-height:110vh;
    max-height: 120vh;
    flex-direction: row;
    justify-content: center;
    margin-top: 25vh;
}

.c-l{
    display: flex;
    width: 30vw;
    height: 40vh;
}

.c-l>:nth-child(1){
    font-size: 5vw;
    width: 19rem;
    height: 15rem;
}

.c-r{
    margin-left: 10vw;
    position: relative;
}

.c-r>form{
    display: flex;
    font-size: 2vw;
    flex-direction: column;
    align-items: center;
    gap: 4vh;
}

.c-r .name{
    width: 28vw;
    height: 4vh;
    border-radius: 8px;
    border:2px solid #dea1ec;
    box-shadow: 1px 1px 4px 4px #dea1ec;
    font-size: 1.5vw;
}

.c-r .name:hover {
    filter: opacity(90%);
    box-shadow: 1px 1px 4px 4px #dea1ec;
}

.c-r .email{
    width: 28vw;
    height: 4vh;
    border-radius: 8px;
    border:2px solid #dea1ec;
    box-shadow: 1px 1px 3px 3px #dea1ec;
    font-size: 1.5vw;
}

.c-r .email:hover {
    filter: opacity(85%);
    box-shadow: 1px 1px 5px 5px #dea1ec;
}

.c-r .message{
    width: 28vw;
    height: 10vh;
    border-radius: 8px;
    border:2px solid #dea1ec;
    box-shadow: 1px 1px 3px 3px #dea1ec;
    font-size: 1.5vw;
}

.c-r .message:hover {
    filter: opacity(90%);
    box-shadow: 1px 1px 6px 6px #dea1ec;
}

.c-r .button{
    width: 12vw;
    height: 8vh;
    border-radius: 8px;
    border:4px solid #dea1ec;
    box-shadow: 1px 1px 3px 3px #dea1ec;
    background: linear-gradient(to top left, #f681d6 0%, #dea1ec 100%);
    font-size: 2vw;
    cursor: pointer;
}

.c-r .button:hover {
    filter: opacity(85%);
    background: linear-gradient(to top left, #f681d6 0%, #eeac99 100%);
    box-shadow: 1px 1px 6px 6px #dea1ec;
}

.shadow{
    float: right;
    width: 40vw;
    height: 60vh;
    position: absolute;
    border-radius: 50%;
    filter: blur(70px);
    z-index: -9;
    background: var(--wp);
}

@media only screen and (max-device-width: 480px) {
    .contact{
        max-width: 90vw;
        min-height:60vh;
        max-height: 73vh;
        flex-direction: column;
        align-items: center;
        margin-top: 15vh;
    }

    .c-l>:nth-child(1){
        position: absolute;
        font-size: 12vw;
        width: 43vw;
    }

    .c-r .name{
        width: 60vw;
        height: 5vh;
        font-size: 5vw;
    }

    .c-r .email{
        width: 60vw;
        height: 5vh;
        font-size: 5vw;
    }

    .c-r .message{
        width: 60vw;
        height: 10vh;
        font-size: 5vw;
    }

    .c-r .button{
        width: 35vw;
        height: 8vh;
        font-size: 8vw;

    }

}

