.DarkMode{
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    align-content: center;
    margin-left: 5vw;
    max-width: 10rem;
    max-height: 4rem;
    border-radius: 3vh;
    border: 3px solid #ff2445;
    padding: 5px 5px;
    position: relative;
    gap: 0.5vw;
}

.DarkMode>*{
    width: 2rem;
    height: 2rem;
}

.d-b{
    border-radius: 100%;
    height: 4.5vh;
    width: 5vh;
    background-color: #ff2445;
    position: absolute;
}

.day{
    background-color: white;
    border-radius: 100%;
    box-shadow: 0px 1px 3px 3px #ff2445;
    max-width: 1.7vw;
    max-height: 1.8vw;
    min-width: 1vh;
    min-height: 1.7vw;
    padding: 2px 4px;
    margin-right: 2px;
}
.night{
    background-color: gray;
    border-radius: 100%;
    box-shadow: 0px 1px 3px 3px #ff2445;
    max-width: 1.7vw;
    max-height: 1.8vw;
    padding: 2px 4px;
    margin-left: 2px;
}

@media only screen and (max-device-width: 480px) {

    .DarkMode>*{
        width: 2rem;
        height: 2rem;
    }

    .d-b{
        border-radius: 100%;
        height: 2.7vh;
        width: 3.2vh;
        background-color: #ff2445;

    }

    .day{
        background-color: white;
        border-radius: 100%;
        box-shadow: 0px 1px 3px 3px #ff2445;
        max-width: 2.5vw;
        max-height: 3vw;
        min-width: 1vh;
        min-height: 1.7vw;
        padding: 2px 4px;
        margin-right: 2px;
    }
    .night{
        background-color: gray;
        border-radius: 100%;
        box-shadow: 0px 1px 3px 3px #ff2445;
        max-width: 2.5vw;
        max-height: 3vw;
        padding: 2px 4px;
        margin-left: 2px;
    }
}