.services{
    display: flex;
    max-width: 100vw;
    min-height:110vh;
    max-height: 120vh;
    flex-direction: column;
    justify-content: center;
    margin-top: 6vh;
}
.s-top {
    display: flex;
    flex-direction: column;
    gap: 1vh;

}
.ms{
    font-size: 8vw;
}

.s-d{
    margin-bottom: 3vh;
}

.s-v{
    font-size: 2.5vw;
    margin-bottom: 4vh;
}

.dl{
    align-content: center;
    justify-content: center;
    align-items: center;
    alignment: center;
    flex-direction: column;
    text-align: center;
    float: right;
    margin: 0;
}


.cv{
    font-size: 2vw;
    border-radius: 25%;
    box-shadow: 1px 1px 5px 5px #d56274;
    background: linear-gradient(to top left, #eeac99 0%, #dea1ec 100%);
    color: white;
    font-weight: bold;
    border: none;
    padding: 2px 17px;
    alignment: center;
    text-decoration: none;
    cursor: pointer;
    text-align: center;
    max-height: 10rem;
    min-height: 2rem;
    min-width: 3rem;
    max-width: 10rem;
}

.cv:hover {
    background: linear-gradient(to top left, #f681d6 0%, #eeac99 100%);
    box-shadow: 1px 1px 7px 7px #d56274;
    cursor: pointer;
}

.cv:active {
    background-color: #eeac99;
}

.cv:focus {
    background-color: #ff2445;
    outline: none;
}
.s-bot {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 10vw;
    margin-bottom: 5vh;
}

.s-bot>:nth-child(1){
    display: flex;
    justify-content: center;
    border-radius: 20%;
    box-shadow: 1px 1px 10px 10px #d6cbd3;
    min-height: 50vh;
    min-width: 20vw;
    max-width: 20vw;
    font-size: 3vw;
    text-align: center;
    flex-direction: column;
}

.s-bot>:nth-child(1):hover{
    filter: opacity(80%);
    box-shadow: 1px 1px 15px 15px #d6cbd3;
}
.s-bot>:nth-child(2){
    display: flex;
    justify-content: center;
    border-radius: 20%;
    box-shadow: 1px 1px 10px 10px #d6cbd3;
    min-height: 50vh;
    min-width: 20vw;
    max-width: 20vw;
    font-size: 3vw;
    text-align: center;
    flex-direction: column;
}
.s-bot>:nth-child(2):hover{
    filter: opacity(80%);
    box-shadow: 1px 1px 15px 15px #d6cbd3;
}

.s-bot>:nth-child(3){
    display: flex;
    justify-content: center;
    border-radius: 20%;
    box-shadow: 1px 1px 10px 10px #d6cbd3;
    min-height: 50vh;
    min-width: 20vw;
    max-width: 20vw;
    font-size: 3vw;
    text-align: center;
    flex-direction: column;
}
.s-bot>:nth-child(3):hover{
    filter: opacity(80%);
    box-shadow: 1px 1px 15px 15px #d6cbd3;
}

@media only screen and (max-device-width: 480px) {

    .ms{
        font-size: 15vw;
    }
    .s-bot {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        gap: 15vw;
        margin-bottom: 5vh;

    }
    .s-bot>:nth-child(1){
        min-height: 8vh;
        min-width: 30vw;
        width: 45vw;
        max-width: 80vw;
        font-size: 2vh;
    }

    .s-bot>:nth-child(2){
        min-height: 8vh;
        min-width: 20vw;
        max-width: 80vw;
        width: 45vw;
        font-size: 2vh;
    }

    .s-bot>:nth-child(3){
        min-height: 8vh;
        min-width: 20vw;
        max-width: 80vw;
        width: 45vw;
        font-size: 2vh;
    }

    .cv{
        font-size: 3.5vh;
        margin-top: 5vh;
    }

    .s-v{
        font-size: 5vw;
        margin-bottom: 4vh;
    }
}
