:root{
  --purple: linear-gradient(180deg, #ff95c2 0%, #d368f3 100%);
  --wp: linear-gradient(to bottom right, #ffffff 20%, #ff2445 100%);
}


.App {
  text-align: center;
  margin: 0;
  font-family: Dongle;
}

@font-face {
  font-family: Dongle;
  src: url("../src/fonts/Dongle/Dongle-Regular.ttf");
}

*{
  font-family: 'Dongle';
  font-weight: normal;
  font-size: 20px;
}

.App-logo {

  height: 6vw;
  pointer-events: none;
  margin: 0;

}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 5s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {

  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.body{
  min-height: 65vh;
  max-height: 75vh;
  max-width: 95vw;
  justify-content: space-between;
  margin: 0;
}

@media only screen and (max-device-width: 480px) {
