
.nav{
    min-height: 4vh;
    max-height: 8vh;
    min-width: 8vw;
    max-width: 100vw;
    font-size: 20px;
    display: flex;
    justify-content: space-between;
    background-color: #d6cbd3;
    margin: 0;

}

.left_nav{
    margin: auto;
    display: flex;
    width: 15%;
    padding: 10px;

}

.right_nav{
    margin: auto;
    width: 50%;
    display: flex;
    flex: 0.8;
    align-items: center;
    justify-content: center;
    display: flex;
}

.list{
    flex: 1;
}

.list>ul{
    display: flex;
    gap: 5vw;
}

.list>ul>li{
    border-radius: 5px;
    color: black;
    font-weight: bold;
    border: none;
    padding: 0vh 10px;
    text-decoration: none;
    display: inline-block;
    font-size: 2vw;
    max-width: 3vw;
    min-width: 2vw;
}

.list>ul>li:hover{
    color: #ff2445;
    cursor: pointer;
}

.but {
    background: linear-gradient(to top, #ff2445 0%, #eeac99 100%);
    color: white;
    font-weight: bold;
    border: none;
    padding: 0px 13px;
    text-decoration: none;
    text-align: center;
    alignment: center;
    font-size: 4vh;
    border-radius: 10px;
    max-height: 6vh;
    min-height: 1vh;
    max-width: 10vw;

}

.but:hover {
    background: linear-gradient(to top, #eeac99 0%, #ff2445 100%);
    cursor: pointer;
}

.but:active {
    background-color: #ff2445;
}

.but:focus {
    background-color: #ff2445;
    outline: none;
}

.but2 {
    box-shadow: 1px 1px 8px 8px #d56274;
    background: linear-gradient(to top, #eeac99 0%, #ff2445 100%);
    color: white;
    font-weight: bold;
    border: none;
    padding: 0px 13px;
    text-decoration: none;
    text-align: center;
    alignment: center;
    font-size: 4vh;
    border-radius: 10px;
    max-height: 6vh;
    min-height: 1vh;
    max-width: 12vh;
    min-width: 10vh;
    margin-bottom: 2.5vh;
    float: right;

}

.but2:hover {
    background: linear-gradient(to top, #ff2445 0%, #eeac99 100%);
    cursor: pointer;
}

.but2:active {
    background-color: #ff2445;
}

.but2:focus {
    background-color: #ff2445;
    outline: none;
}


@media only screen and (max-device-width: 480px) {
    .but{
        width: 10vw;
        height: 3vh;
        font-size: 4vw;
    }

    .list>ul{
        display: flex;
        gap: 2vw;
    }

    .list>ul>li{
        color: black;
        font-weight: bold;
        border: none;
        padding: 0vh 10px;
        text-decoration: none;
        display: inline-block;
        font-size: 3vw;
        max-width: 3vw;
        min-width: 2vw;
    }

    .but2{
        box-shadow: 1px 1px 4px 4px #d56274;
        min-width: 10vw;
        width: 12.5vw;
        max-width: 15vw;
        font-size: 3.5vw;
        height: 3vh;
        margin-bottom: 0.1vh;
    }
}





