.portfolio{
    display: flex;
    max-width: 100vw;
    min-height:110vh;
    max-height: 120vh;
    flex-direction: column;
    justify-content: center;
}


.portfolio>:nth-child(2){
    font-size: 10vw;
}

.portfolio>:nth-child(3){
    margin-bottom: 10vh;
    font-size: 2vw;
}

.projects{
    display: flex;
    flex-direction: row;
    gap: 4vw;
    width: 80vw;
    justify-content: center;
    margin-left: 9vw;
}

.projects>:nth-child(1){
    display: flex;
    justify-content: center;
    border-radius: 20%;
    box-shadow: 1px 1px 10px 10px #d6cbd3;
    min-height: 20vh;
    min-width: 12vw;
    max-width: 15vw;
    font-size: 2.5vw;
    align-items: center;
    text-align: center;
    color: inherit;
}

.projects>:nth-child(1):hover{
    filter: opacity(80%);
    box-shadow: 1px 1px 15px 15px #d6cbd3;
}

.projects>:nth-child(2){
    display: flex;
    justify-content: center;
    border-radius: 20%;
    box-shadow: 1px 1px 10px 10px #d6cbd3;
    min-height: 20vh;
    min-width: 12vw;
    max-width: 15vw;
    font-size: 3vw;
    align-items: center;
    text-align: center;
    color: inherit;
}

.projects>:nth-child(2):hover{
    filter: opacity(80%);
    box-shadow: 1px 1px 15px 15px #d6cbd3;
}

.projects>:nth-child(3){
    display: flex;
    justify-content: center;
    border-radius: 20%;
    box-shadow: 1px 1px 10px 10px #d6cbd3;
    min-height: 20vh;
    min-width: 12vw;
    max-width: 15vw;
    font-size: 3vw;
    align-items: center;
    text-align: center;
    color: inherit;
}

.projects>:nth-child(3):hover{
    filter: opacity(80%);
    box-shadow: 1px 1px 15px 15px #d6cbd3;
}

@media only screen and (max-device-width: 480px) {
    .portfolio>:nth-child(2){
        font-size: 15vw;
    }

    .portfolio>:nth-child(3){
        margin-bottom: 10vh;
        font-size: 6vw;
    }

    .projects{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 6vh;
        width: 80vw;
        justify-content: center;
        margin-top: 5vh;
    }

    .projects>:nth-child(1){
        display: flex;
        justify-content: center;
        color: inherit;
        min-height: 8vh;
        min-width: 30vw;
        width: 45vw;
        max-width: 80vw;
        font-size: 2vh;
    }
    .projects>:nth-child(2){
        display: flex;
        justify-content: center;
        color: inherit;
        min-height: 8vh;
        min-width: 30vw;
        width: 45vw;
        max-width: 80vw;
        font-size: 2vh;
    }
    .projects>:nth-child(3){
        display: flex;
        justify-content: center;
        color: inherit;
        min-height: 8vh;
        min-width: 30vw;
        width: 45vw;
        max-width: 80vw;
        font-size: 2vh;
    }
}