.App-footer {
    background-color: #d6cbd3;
    min-height: 14vh;
    max-height: 20vh;
    display: flex;
    justify-content: space-between;
    font-size: calc(10px + 2vmin);
    color: white;
    margin: 0;

}

.left_foot{
    margin-left: 4vw;
    height: 7vh;
    width: 15vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.left_foot>:nth-child(1){
    font-size: 2.5vw;
    color: black;
}

.left_foot>:nth-child(2){
    font-size: 2vw;
    color: black;
}

.center_foot{
    min-height: 5vh;
    max-height: 20vh;
    min-width: 20vw;
    max-width: 30vw;
    text-align: center;
}

.r{
    float:right;
    margin-top: 8vh;
    margin-right: 2vw;
}
.r>:nth-child(1){
    font-size: 1.5vw;
    color: black;
}

@media only screen and (max-device-width: 480px) {
    .left_foot>:nth-child(1){
        font-size: 8vw;
        color: black;
        margin-left: 20vw;
    }

    .left_foot>:nth-child(2){
        font-size: 6vw;
        color: black;
        margin-left: 20vw;
    }

    .r>:nth-child(1){
        font-size: 5vw;
        color: black;
    }

}